import React, { Component } from 'react';
import { Button } from 'antd';

class Dog extends Component {
    render(){
        const { count, addDog } = this.props;
        return (
            <div>
                <p>{ count }</p>
                <Button type="primary" onClick={addDog}>1</Button>
            </div>
        )
    }
}

export default Dog;
