import Mock from 'mockjs' // mockjs

const Random = Mock.Random // Mock.Random 

let data = [] // 
let size = [
  '300x250', '250x250', '240x400', '336x280',
  '180x150', '720x300', '468x60', '234x60',
  '88x31', '120x90', '120x60', '120x240',
  '125x125', '728x90', '160x600', '120x600',
  '300x600'
] // 
for(let i = 0; i < 10; i ++) { // 
  let template = {
    'Boolean': Random.boolean, // 
    'Natural': Random.natural(1, 10), // 1100
    'Integer': Random.integer(1, 100), // 1100
    'Float': Random.float(0, 100, 0, 5), // 0100,05
    'Character': Random.character(), // ,
    'String': Random.string(2, 10), // 210
    'Range': Random.range(0, 10, 2), // 
    'Date': Random.date(), // ,
    'Image': Random.image(Random.size, '#02adea', 'Hello'), // Random.sizesize
    'Color': Random.color(), // 
    'Paragraph':Random.paragraph(2, 5), //25
    'Name': Random.name(), // 
    'Url': Random.url(), // web
    'Address': Random.province() // 
  }
  data.push(template)
}

Mock.mock('/data/index', 'post', data) // 
