import React from 'react';
import {
    Table,
    Divider,
    Form,
    Input,
    Radio,
    Modal,
    message,
    Button,
    Popconfirm,
} from 'antd';
//  antd 
import 'antd/dist/antd.css';
import { movieList } from '../mock/movieList';
import api from './../axios/api';
import axios from 'axios';

const { Column, ColumnGroup } = Table;

class StudentList extends React.Component {
    state = {
        studentList: [],
        movieList,
        modalVisible: false,
        selectedStudents: [],
        modalType: 'add',
    };

    componentDidMount() {
        api.mockdata('/data/index').then(res => {
            console.log(res);
        });
        this.getStudentList();
    }

    getStudentList = () => {
        axios.get('http://127.0.0.1:3004/student/list').then(res => {
            if (res.data.code === 1) {
                this.setState({
                    studentList: res.data.data,
                });
            }
        });
    };

    showModal = (id = -1) => {
        const {
            form: { setFieldsValue, resetFields },
        } = this.props;
        const { studentList } = this.state;
        let newState = {};
        resetFields();
        if (id === -1) {
            // 
            newState = {
                modalVisible: true,
                modalType: 'add',
            };
        } else {
            // 
            let currentStudent = {};
            studentList.forEach(item => {
                if (item.id === id) {
                    currentStudent = item;
                }
            });
            newState = {
                modalVisible: true,
                modalType: 'edit',
                currentStudent: id,
            };
            setFieldsValue(currentStudent);
        }
        this.setState(newState);
    };

    handleOk = () => {
        const { studentList, currentStudent, modalType } = this.state;
        const {
            form: { validateFields },
        } = this.props;
        validateFields((err, values) => {
            if (err) {
                return;
            }
            const newStudents = Array.from(studentList);
            if (modalType === 'add') {
                // 
                newStudents.push(values);
            } else {
                // 
                let studentIndex = -1;
                newStudents.forEach((item, index) => {
                    if (item.id === currentStudent) {
                        studentIndex = index;
                    }
                });
                newStudents.splice(studentIndex, 1, values);
            }
            this.setState({
                students: newStudents,
                modalVisible: false,
            });
        });
    };

    handleCancel = () => {
        this.setState({
            modalVisible: false,
        });
    };

    deleteStudents = () => {
        const { selectedStudents } = this.state;
        if (selectedStudents.length === 0) {
            message.error('');
        } else {
            axios
                .get('http://127.0.0.1:3004/student/delete', {
                    params: {
                        students: selectedStudents,
                    },
                })
                .then(res => {
                    if (res.data.code === 1) {
                        this.getStudentList();
                    }
                });
            this.setState({
                selectedStudents: [],
            });
        }
    };

    deleteOneStudent = (id, e) => {
        axios
            .get('http://127.0.0.1:3004/student/delete', {
                params: {
                    students: [id],
                },
            })
            .then(res => {
                if (res.data.code === 1) {
                    this.getStudentList();
                }
            });
    };

    render() {
        const { studentList, modalVisible } = this.state;
        const {
            form: { getFieldDecorator, getFieldError, isFieldTouched },
        } = this.props;
        // Only show error after a field is touched.
        const idError = isFieldTouched('id') && getFieldError('id');
        const nameError = isFieldTouched('name') && getFieldError('name');
        const ageError = isFieldTouched('age') && getFieldError('age');
        const sexError = isFieldTouched('sex') && getFieldError('sex');

        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 8 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 16 },
            },
        };

        // rowSelection object indicates the need for row selection
        const rowSelection = {
            onChange: (selectedRowKeys, selectedRows) => {
                // console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
                this.setState({
                    selectedStudents: selectedRowKeys,
                });
            },
            getCheckboxProps: record => ({
                disabled: record.name === 'Disabled User', // Column configuration not to be checked
                name: record.name,
            }),
        };

        const tableTitle = (
            <div>
                <span>
                    
                    {studentList.length}
                    
                </span>
                <br />
                <br />
                <Button.Group>
                    <Button
                        type="primary"
                        icon="delete"
                        onClick={this.deleteStudents}
                    >
                        
                    </Button>
                    <Button
                        type="primary"
                        icon="plus"
                        onClick={() => {
                            this.showModal();
                        }}
                    >
                        
                    </Button>
                </Button.Group>
            </div>
        );

        return (
            <div>
                <Table
                    bordered
                    rowKey="id"
                    dataSource={studentList}
                    rowSelection={rowSelection}
                    title={() => tableTitle}
                >
                    <Column title="ID" dataIndex="id" />
                    <Column title="" dataIndex="name" />
                    <Column
                        title=""
                        dataIndex="sex"
                        render={sex => <span>{sex === 1 ? '' : ''}</span>}
                    />
                    <Column title="" dataIndex="age" />
                    <Column title="" dataIndex="birthday" />
                    <Column
                        title=""
                        dataIndex="color"
                        render={color => (
                            <i
                                style={{
                                    display: 'inline-block',
                                    width: '20px',
                                    height: '20px',
                                    backgroundColor: color,
                                }}
                            />
                        )}
                    />
                    <Column title="" dataIndex="constellation" />
                    <Column title="" dataIndex="address" />
                    <Column
                        title=""
                        key="Action"
                        render={(text, record) => (
                            <span>
                                <a
                                    href="javascript:;"
                                    onClick={e => this.showModal(record.id, e)}
                                >
                                    
                                </a>
                                <Divider type="vertical" />
                                <Popconfirm
                                    title=""
                                    onConfirm={e => {
                                        this.deleteOneStudent(record.id, e);
                                    }}
                                >
                                    <a href="javascript:;"></a>
                                </Popconfirm>
                            </span>
                        )}
                    />
                </Table>
                {/*  */}
                <Modal
                    title=""
                    visible={modalVisible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                >
                    <div style={{ width: '400px' }}>
                        <Form>
                            <Form.Item
                                {...formItemLayout}
                                label="ID"
                                validateStatus={idError ? 'error' : ''}
                                help={idError || ''}
                            >
                                {getFieldDecorator('id', {
                                    rules: [
                                        { required: true, message: 'id' },
                                    ],
                                })(
                                    <Input
                                        placeholder="ID"
                                        disabled={
                                            this.state.modalType === 'edit'
                                        }
                                    />,
                                )}
                            </Form.Item>
                            <Form.Item
                                {...formItemLayout}
                                label=""
                                validateStatus={nameError ? 'error' : ''}
                                help={nameError || ''}
                            >
                                {getFieldDecorator('name', {
                                    rules: [
                                        {
                                            required: true,
                                            message: '',
                                        },
                                    ],
                                })(<Input placeholder="" />)}
                            </Form.Item>
                            <Form.Item
                                {...formItemLayout}
                                label=""
                                validateStatus={ageError ? 'error' : ''}
                                help={ageError || ''}
                            >
                                {getFieldDecorator('age', {
                                    rules: [
                                        {
                                            required: true,
                                            message: '',
                                        },
                                    ],
                                })(<Input type="number" placeholder="" />)}
                            </Form.Item>
                            <Form.Item
                                {...formItemLayout}
                                label=""
                                validateStatus={sexError ? 'error' : ''}
                                help={sexError || ''}
                            >
                                {getFieldDecorator('sex')(
                                    <Radio.Group buttonStyle="solid">
                                        <Radio.Button value={1}>
                                            
                                        </Radio.Button>
                                        <Radio.Button value={0}>
                                            
                                        </Radio.Button>
                                    </Radio.Group>,
                                )}
                            </Form.Item>
                        </Form>
                    </div>
                </Modal>
            </div>
        );
    }
}

export default Form.create()(StudentList);
