// state
class Clock extends React.Component {
    constructor(props) {
        super(props);
        this.state = { date: new Date() }
    }
    componentDidMount() {
        // 
        this.timeID = setInterval(
            () => this.tick(), 1000
        )
    }
    componentWillUnmount() {
        // 
        clearInterval(this.timeID)
    }
    tick() {
        this.setState({
            date: new Date()
        })
    }
    render() {
        return (
            <div>
                <h6>{this.state.date.toLocaleTimeString()}</h6>
                <p>
                    name: {this.props.name}
                </p>
            </div>
        )
    }
}

// 
Clock.defaultProps = {
    name: 'Tom'
}

// props prop-types
Clock.propTypes = {
    name: PropTypes.string
};

// 
function ActionLink() {
    function handleClick(e) {
        e.preventDefault();
        console.log('')
    }
    return (
        <a href="/" onClick={handleClick}>
            
      </a>
    )
}

// 
class Toggle extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            name: 'Lilisi',
            isToggleOn: false
        }
        // 1.  `this` 
        this.handleClick = this.handleClick.bind(this)
    }
    handleClick(name, e) {
        // 2. 
        // this.setState
        this.setState(prevState => ({
            isToggleOn: !prevState.isToggleOn
        }))
        console.log(name)
    }
    render() {
        return (
            // 3.  (e) => {this.handleClick(e)}
            //   (e) => this. deleteRow(id, e) || this.deleteRow.bind(this, id)
            <button onClick={this.handleClick.bind(this, this.state.name)}>
                {this.state.isToggleOn ? 'ON' : 'OFF'}
            </button>
        )
    }
}

// 
class Name extends React.Component {
    render() {
        return <p>name: {this.props.name}</p>
    }
}

class Link extends React.Component {
    render() {
        return <a href={this.props.site}>{this.props.site}</a>
    }
}

class WebSite extends React.Component {
    constructor() {
        super()
        this.state = {
            name: 'Google',
            site: 'www.google.com'
        }
    }
    render() {
        return (
            <div>
                <Name name={this.state.name} />
                <Link site={this.state.site} />
            </div>
        )
    }
}

