class SearchStudent extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            item: ''
        }
        this.handleChange = this.handleChange.bind(this)
    }
    handleChange(e){
        this.setState({
            item: e.target.value
        })
    }
    render() {
        return (
            <div>
                <input value={this.state.item} onChange={this.handleChange} />
                {/* <StudentList filterValue={this.state.item} /> */}
                <ul>
                    {students.map(item => {
                        return item.name.includes(this.state.item) ? <li key={item.id}>{item.name}</li> : ''
                    })}
                </ul>
            </div>
        )
    }
}

// 
class Button extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            data: 0
        }
        this.setNewData = this.setNewData.bind(this)
    }
    setNewData() {
        this.setState({ data: this.state.data + 1 })
    }
    render() {
        return (
            <div>
                <h1 style={{ color: 'darkblue', fontWeight: 'bolder' }}></h1>
                <button onClick={this.setNewData}>1</button>
                <Content myNumber={this.state.data} />
            </div>
        )
    }
}

