// 
class HelloMessage extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            value: 'Hello ',
            teacherName: 'Jack'
        }
        this.handleChange = this.handleChange.bind(this)
        this.handleChangeTeacher = this.handleChangeTeacher.bind(this)
    }
    handleChange(event) {
        this.setState({ value: event.target.value })
    }
    handleChangeTeacher(name, e){
        this.setState(
            {
                teacherName: name
            }
        )
        console.log(name)
    }
    render() {
        return (
            <div>
                <h1 style={{ color: 'darkblue', fontWeight: 'bolder' }}></h1>
                <HelloMessageContent from={this.state.teacherName} value={this.state.value} updateStateProp={this.handleChange}>
                    <div>children1</div>
                </HelloMessageContent>
                <Teacher name={this.state.teacherName} changeTeacherName={this.handleChangeTeacher} />
            </div>
        )
    }
}

class HelloMessageContent extends React.Component {
    render() {
        return (
            <div>
                <input type="text" value={this.props.value} onChange={this.props.updateStateProp} />
                <p>
                    {this.props.value} from{this.props.from}
                </p>
                {this.props.children}
            </div>
        )
    }
}

class Teacher extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            teacherName: this.props.name
        }
        this.handleTeacherChange = this.handleTeacherChange.bind(this)
    }
    handleTeacherChange(e){
        this.setState({
            teacherName: e.target.value
        })
    }
    render(){
        return (
            <div>
                <input type="text" value={this.state.teacherName} onChange={this.handleTeacherChange} />
                <button onClick={this.props.changeTeacherName.bind(this, this.state.teacherName)}></button>
            </div>
        )
    }
}

class FlavorForm extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            value: 'Apple'
        }

        this.handleChange = this.handleChange.bind(this)
        this.handleSubmit = this.handleSubmit.bind(this)
    }

    handleChange(event) {
        this.setState({ value: event.target.value })
    }
    handleSubmit(event) {
        console.log(' ' + this.state.value)
        event.preventDefault()
    }

    render() {
        return (
            <div>
                <form onSubmit={this.handleSubmit}>
                    <select value={this.state.value} onChange={this.handleChange}>
                        <option value="Google">Google</option>
                        <option value="Apple"></option>
                        <option value="MW"></option>
                        <option value="FB">Facebook</option>
                    </select>
                    <input type="submit" value="" />
                </form>
            </div>
        )
    }
}

